import React from "react";
import { highlightType } from "./function-highlighter";

const SyntaxHighlighter = ({ name, definition }) => {
  const highlightSyntax = (code) => {
    return code
      .replace(/\b(type)\b/g, '<span class="keyword">$1</span>')
      .replace(/(--.*)$/gm, '<span class="comment">$1</span>');
  };

  const highlightTypeDefinition = (typeDef) => {
    const parts = typeDef.split(/([&|])/);
    
    return parts.map((part, _) => {
      if (part === '&' || part === '|') {
        return part;
      }

      const whitespaceBefore = part.match(/^\s*/)[0];
      const whitespaceAfter = part.match(/\s*$/)[0];
      return `${whitespaceBefore}${highlightType(part.trim())}${whitespaceAfter}`;
    }).join('');
  };

  let output = "";

  if (typeof definition === "string") {
    const baseType = definition.replace(/\?$/, "");
    const isOptional = definition.endsWith("?");
    const highlightedType = highlightTypeDefinition(baseType);
    output = `type ${name} = ${highlightedType}${isOptional ? "?" : ""};`;
  } else if (typeof definition === "object") {
    const formattedProps = Object.entries(definition)
      .map(([key, value]) => {
        let type = "";
        let comment = "";

        if (typeof value === "string") {
          type = highlightTypeDefinition(value.replace(/\?$/, "")) + 
                (value.endsWith("?") ? "?" : "");
        } else if (typeof value === "object" && value.type) {
          type = highlightTypeDefinition(value.type.replace(/\?$/, "")) + 
                (value.type.endsWith("?") ? "?" : "");
          comment = value.comment ? ` -- ${value.comment}` : "";
        }

        return `    ${key}: ${type};${comment}`;
      })
      .join("\n");

    output = `type ${name} = {\n${formattedProps}\n}`;
  } else {
    output = "/* Invalid Input */";
  }

  return (
    <pre className="output" dangerouslySetInnerHTML={{ __html: highlightSyntax(output) }} />
  );
};

export default SyntaxHighlighter;