import React from "react";
import '../Api.css';
import Footer from "../../components/footer";
import APITopbar from "../components/api-topbar";
import Sidebar from "../components/sidebar";
import "../css/api-sidebar.css";
import Type from "../components/api-type";
import Prop from "../components/api-prop";
import Func from "../components/api-function";
import SidebarInfo from "../sidebar-info";
import ClassMeta from "../components/class-meta";

const Result = () => {
    return (
        <div className="API">
            <title>API | OSGL</title>
            <APITopbar />

            <div className="sidebar-content">
                <Sidebar links={SidebarInfo} />
            </div>

            <div className="api-content">
                <h1 className="api-header">Result</h1>
                <div className="api-header-line"></div>

                <ClassMeta metadata={{
                    core: true,
                    threadSafe: true,
                    abstract: true,
                }} />

                <p className="api-paragraph">Based of Rust's <code>Result</code> item, used for error-handling.
                    Cannot be accessed by default. Can be required with <code>require(path.to.OSGL.result)</code>, and be created with <code>Result.new</code>.
                    Returned by most, if not all OSGL "checked" functions.
                </p>

                <h1 className="api-header">Properties</h1>
                <div className="api-header-line"></div>

                <Prop name="isOk" fullName="Result.isOk" type="boolean" desc="Whether the value is an `Err` or not." />
                <Prop name="value" fullName="Result.value" type="any" desc="The value stored." />

                <h1 className="api-header">Methods</h1>
                <div className="api-header-line"></div>

                <Func
                    name="Unwrap"
                    isMethod={true}
                    fullName="Result:Unwrap"
                    params={{}}
                    returnType="T"
                    desc="Returns the Ok value if there is one, else errors."
                    metadata={{
                        Debug: true,
                        Experimental: true,
                        ThreadSafe: true,
                    }}
                />

                <Func
                    name="Expect"
                    isMethod={true}
                    fullName="Result:Expect"
                    params={{
                        message: "string"
                    }}
                    returnType="T"
                    desc="Errors with the given message if the value is an Err, else returns the Ok value."
                    metadata={{
                        Debug: true,
                        Experimental: true,
                        ThreadSafe: true,
                    }}
                />

                <Func
                    name="UnwrapErr"
                    isMethod={true}
                    fullName="Result:UnwrapErr"
                    params={{}}
                    returnType="E"
                    desc="Returns the Err value."
                    metadata={{
                        Debug: true,
                        Experimental: true,
                        ThreadSafe: true,
                    }}
                />

                <Func
                    name="ExpectErr"
                    isMethod={true}
                    fullName="Result:ExpectErr"
                    params={{
                        message: "string"
                    }}
                    returnType="E"
                    desc="Errors with the given message if the value is Ok, else returns the Err value."
                    metadata={{
                        Debug: true,
                        Experimental: true,
                        ThreadSafe: true,
                    }}
                />

                <Func
                    name="UnwrapOr"
                    isMethod={true}
                    fullName="Result:UnwrapOr"
                    params={{
                        value: "T"
                    }}
                    returnType="T"
                    desc="Returns the Ok value, or the given one if it is an Err"
                    metadata={{
                        Debug: true,
                        Experimental: true,
                        ThreadSafe: true,
                    }}
                />

<Func
                    name="Or"
                    isMethod={true}
                    fullName="Result:Or"
                    params={{
                        result: "Result"
                    }}
                    returnType="Result<T, E>"
                    desc="Returns the already-existing Result, or the one provided if the value is an Err."
                    metadata={{
                        Debug: true,
                        Experimental: true,
                        ThreadSafe: true,
                    }}
                />

                <div style={{ height: "55px" }} />
            </div>
            <Footer />
            <div className="grid-background-dark"></div>
        </div>
    );
};

export default Result;
